import { extent, max, min } from "d3-array";
/*
It takes two arguments; data & dims. You can imagine perhaps using the data parameter for setting up your scales
domains, but we wont bother in this example. Also perhaps you could add a range parameter, there are lots of
possibilities, but for now lets keep it simple.
 */
import { scaleLinear } from "d3-scale";
class Scales {
  constructor(data, dims) {
    this.setScales(data, dims);
  }
  setScales = (data, dims) => {
    this.xScale = scaleLinear().domain([0, 9]).range([0, dims.innerWidth]);
    this.yScale = scaleLinear().domain([0, 10]).range([dims.innerHeight, 0]);
  };
}
export default Scales;
